import { useState, useEffect } from "react";
import { Button, Form, ButtonGroup, Alert } from "react-bootstrap";
import { InputText } from "./InputText";

export const Login = ({ user, onSubmit, login, onSignupClick }) => {
	const [input, setInput] = useState({ usuarioLector: user.usuarioLector, password: user.password });
	const [show, setShow] = useState(false);

	const onDataSend = (succes) => {
		if (succes) {
			return (
				<>
					<Alert variant="success">Usuario encontrado</Alert>
				</>
			);
		} else {
			return (
				<>
					<Alert variant="danger">Usuario no encontrado</Alert>
				</>
			);
		}
	};

	useEffect(() => {
		setInput({ usuarioLector: user.usuarioLector, password: user.password });
	}, [user]);


	return (
		<fieldset
			style={{
				boxShadow: "2px 4px 6px 0px rgba(0,0,0,0.75)",
				padding: "1em",
				width: "100%",
			}}
		>
			<legend align="left">Login</legend>
			<Form onSubmit={(e) => onSubmit(e, input)} style={{ textAlign: "left" }}>

				{user.usuarioLector ? (
					<InputText
						title="User"
						type="text"
						value={input.usuarioLector}
						placeholder="Enter user"
						onChange={(e) => {

							setInput({ ...input, usuarioLector: e.target.value });
						}}
					/>
				) : (
					<InputText
						title="User"
						type="text"
						placeholder="Enter user"
						onChange={(e) => {

							setInput({ ...input, usuarioLector: e.target.value });
						}}
					/>
				)}

				{user.password ? (
					<InputText
						title="Password"
						type="password"
						value={input.password}
						placeholder="Enter password"
						onChange={(e) => {
							setInput({ ...input, password: e.target.value });
						}
						}
					/>
				) : (
					<InputText
						title="Password"
						type="password"
						placeholder="Enter password"
						onChange={(e) => {
							setInput({ ...input, password: e.target.value });
						}}
					/>
				)}
				<ButtonGroup className="mb-3" aria-label="Basic example">
					<Button
						type="submit"
						onClick={() => setTimeout(() => setShow(true), 500)}
					>
						Login
					</Button>
					<Button onClick={(e) => { onSignupClick() }} >Register</Button>
				</ButtonGroup>
				{show ? onDataSend(login) : null}
			</Form>
		</fieldset>
	);
};
