import { useState, useEffect } from "react";
import { Accordion } from "react-bootstrap";
import { Stack } from "react-bootstrap";
import { Button } from "react-bootstrap";
import { Form } from "react-bootstrap";
import SettingsIcon from '@mui/icons-material/Settings';

export const MainSite = ({ loggedUser, onCloseSession, onEditUser, librosDisponibles, librosPrestados, devolverLibro, prestarLibro }) => {

    const [searchedBooks, setSearchedBooks] = useState([...librosDisponibles]);

    const onSearch = (e) => {
        if (e.target.value.length >= 3) {
            const search = e.target.value;
            const filteredBooks = librosDisponibles.filter((libro) => {
                return libro.titulo.toLowerCase().includes(search.toLowerCase());
            });
            setSearchedBooks(filteredBooks);
        } else {
            setSearchedBooks(librosDisponibles);
        }
    };

    useEffect(() => {
        setSearchedBooks(librosDisponibles);
    }, [librosDisponibles]);


    return (
        <div style={{ 'width': '100%' }}>
            <div className="d-flex justify-content-between">
                <h1>Bienvenido {loggedUser.nombreLector}</h1>
                <div>
                    <Button variant="secondary" className="me-2" onClick={() => onEditUser()}>
                        <SettingsIcon />
                    </Button>
                    <Button variant="danger" onClick={() => onCloseSession()}>Cerrar sesion</Button>
                </div>
            </div>
            <Accordion defaultActiveKey="0">
                <Accordion.Item eventKey="0">
                    <Accordion.Header>Libros disponibles</Accordion.Header>
                    <Accordion.Body>
                        <Form style={{ 'textAlign': 'left' }} >
                            <Form.Group className="mb-3" controlId="formBookSearch">
                                <Form.Label  >Buscar libro</Form.Label>
                                <Form.Control type="text" placeholder="Introduce el titulo del libro"
                                    onChange={(e) => onSearch(e)} />
                            </Form.Group>
                        </Form>
                        <Stack>
                            {
                                searchedBooks.map((libro) => {
                                    return (
                                        <div className="bg-light border p-2" key={libro.id} style={{ 'textAlign': 'left' }}>
                                            <h3>{libro.titulo}</h3>
                                            <p><b>Autor:</b> {libro.autor}</p>
                                            <Button variant="success" onClick={
                                                () => {
                                                    prestarLibro(libro);
                                                }}
                                            >Tomar prestado</Button>
                                        </div>
                                    )
                                })
                            }
                        </Stack>
                    </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey="1">
                    <Accordion.Header>Libros prestados</Accordion.Header>
                    <Accordion.Body>
                        <Stack>
                            {
                                librosPrestados.map((libro) => {
                                    return (
                                        <div className="bg-light border p-2" key={libro.id} style={{ 'textAlign': 'left' }}>
                                            <h3>{libro.titulo}</h3>
                                            <p><b>Autor:</b> {libro.autor}, <b>Devolución:</b> {libro.fecha_devolucion}</p>
                                            <Button variant="danger" onClick={
                                                () => {
                                                    devolverLibro(libro);
                                                }}
                                            >Devolver</Button>
                                        </div>
                                    )
                                })
                            }
                        </Stack>
                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
        </div>
    );
};
