import "./App.css";
import { useEffect, useState } from "react";
import { Login } from "./components/Login";
import { MainSite } from "./components/MainSite";
import { Signup } from "./components/Signup";
import { EditUser } from "./components/EditUser";
import { URL } from "./context/json-server-url";
function App() {
	const [user, setUser] = useState({ usuarioLector: "", password: "" });
	const [loggedUser, setLoggedUser] = useState(undefined);
	const [login, setLogin] = useState(false);
	const [librosDisponibles, setLibrosDisponibles] = useState([]);
	const [librosPrestados, setLibrosPrestados] = useState([]);
	const [signup, setSignup] = useState(false);
	const [editUser, setEditUser] = useState(false);

	const closeSession = () => {
		setLoggedUser(undefined);
		setLogin(false);
		setLibrosDisponibles([]);
		setLibrosPrestados([]);

	};

	const loadFromLocalStorage = () => {
		if (localStorage.getItem("user")) {
			const userLocalStorage = JSON.parse(localStorage.getItem("user"));

			setUser(userLocalStorage);
		}
	}

	const prestarLibro = async (libro) => {
		const currentDate = new Date();
		const fecha_devolucion = new Date(currentDate.getTime() + (15 * 24 * 60 * 60 * 1000)).toLocaleDateString();
		const fetchResult = await fetch(`${URL}/libros/${libro.id}`, {
			method: "PATCH",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify({
				id_prestamo: loggedUser.id,
				fecha_devolucion: `${fecha_devolucion}`,
			})
		});
		const libroPrestado = await fetchResult.json();
		console.log('prestar libro');
		console.log(libroPrestado);

		setLibrosDisponibles(librosDisponibles.filter((libroDisponible) => libroDisponible.id !== libro.id));
		setLibrosPrestados([...librosPrestados, libroPrestado]);
	};

	const devolverLibro = async (libro) => {
		const fetchResult = await fetch(`${URL}/libros/${libro.id}`, {
			method: "PATCH",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify({
				id_prestamo: 0,
				fecha_devolucion: ""
			})
		});
		const libroDevuelto = await fetchResult.json();
		console.log('devolverLibro');
		console.log(libroDevuelto);
		setLibrosPrestados(librosPrestados.filter((libroPrestado) => libroPrestado.id !== libro.id));
		setLibrosDisponibles([...librosDisponibles, libroDevuelto]);
	};


	const clearUser = () => {
		setUser({
			nombreLector: undefined,
			apellidosLector: undefined,
			usuarioLector: undefined,
			email: undefined,
			password: undefined,
			id: undefined,
		});
	};

	const loadBooks = async (id) => {
		const fetchResultLibrosDisponibles = await fetch(`${URL}/libros?id_prestamo=0`);
		const dataLibrosDisponibles = await fetchResultLibrosDisponibles.json();
		setLibrosDisponibles(dataLibrosDisponibles);

		const fetchResultLibrosPrestados = await fetch(`${URL}/libros?id_prestamo=${id}`);
		const dataLibrosPrestados = await fetchResultLibrosPrestados.json();
		setLibrosPrestados(dataLibrosPrestados);

	}
	const onLoginSubmit = async (e, user) => {
		console.log(user);
		e.preventDefault();
		const { usuarioLector, password } = user;
		const fetchResult = await fetch(
			`${URL}/usuarios?usuarioLector=${usuarioLector}&password=${password}`
		);
		const data = await fetchResult.json();

		if (data.length > 0) {
			localStorage.setItem("user", JSON.stringify(data[0]));
			loadBooks(data[0].id);
			setLogin(true);

			setTimeout(() => {
				setLoggedUser(data[0]); //i wait 3 seconds to load the user page
			}, 1500);

			console.log(data[0]);

		} else {
			setLogin(false);
			setTimeout(() => { }, 1500);
			clearUser();
			e.target.reset();
		}
	};

	const onSignupClick = () => {
		setSignup(true);
	};

	const onSignup = async (user) => {

		console.log(user)

		const { nombreLector, apellidosLector, usuarioLector, email, password } = user;
		const fetchResult = await fetch(`${URL}/usuarios`, {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify({
				nombreLector: `${nombreLector}`,
				apellidosLector: `${apellidosLector}`,
				usuarioLector: `${usuarioLector}`,
				email: `${email}`,
				password: `${password}`,
			}),
		});
		const data = await fetchResult.json();
		console.log(data);
		setSignup(false);
		clearUser();

	};

	const onSignupClose = () => {
		setSignup(false);
	};

	const onEditUser = async () => {
		setEditUser(true);
	}

	const onEditUserSubmit = async (user) => {

		const { nombreLector, apellidosLector, usuarioLector, email, password } = user;
		const fetchResult = await fetch(`${URL}/usuarios/${loggedUser.id}`, {
			method: "PATCH",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify({
				nombreLector: `${nombreLector}`,
				apellidosLector: `${apellidosLector}`,
				usuarioLector: `${usuarioLector}`,
				email: `${email}`,
				password: `${password}`,
			}),
		});
		const data = await fetchResult.json();

		setLoggedUser(data);
		setEditUser(false);
		localStorage.setItem("user", JSON.stringify(data));
	}

	const onCloseEditUser = () => {
		setEditUser(false);
	}

	useEffect(() => {
		loadFromLocalStorage();
	}, []);



	return (
		<div className="App">
			{loggedUser ?

				editUser ?
					(
						<EditUser loggedUser={loggedUser} onSubmitClick={onEditUserSubmit} onClose={onCloseEditUser} />
					)
					:
					(
						<MainSite onEditUser={onEditUser} onCloseSession={closeSession} loggedUser={loggedUser} librosDisponibles={librosDisponibles} librosPrestados={librosPrestados} prestarLibro={prestarLibro} devolverLibro={devolverLibro} />
					)

				:

				signup ?
					(
						<Signup onSubmitClick={onSignup} onClose={onSignupClose} />
					)
					:
					(
						<Login user={user} onSubmit={onLoginSubmit} login={login} onSignupClick={onSignupClick} loadFromLocalStorage={loadFromLocalStorage} />
					)

			}

		</div>
	);
}

export default App;
				//             }}