
import { Button, Form, ButtonGroup, Alert, CloseButton } from 'react-bootstrap';
import { useState } from 'react';
import { URL } from '../context/json-server-url';

export const Signup = ({ onSubmitClick, onClose }) => {
    const [usernameExist, setUsernameExist] = useState(false);
    const [emailExist, setEmailExist] = useState(false);

    const checkUsernameExists = async (e) => {
        const username = e.target.value;
        const fetchResult = await fetch(`${URL}/usuarios?usuarioLector=${username}`);
        const data = await fetchResult.json();
        if (data.length > 0 && username.length > 2) {
            e.target.setCustomValidity("Username already exists");
            setUsernameExist(true);
        } else {
            setUsernameExist(false);
        }
    }

    const checkEmailExists = async (e) => {
        const email = e.target.value;
        const fetchResult = await fetch(`${URL}/usuarios?email=${email}`);
        const data = await fetchResult.json();
        if (data.length > 0 && email.length > 9) {
            e.target.setCustomValidity("Email already exists");
            setEmailExist(true);

        } else {
            setEmailExist(false);
        }
    }

    const checkPassowrds = (e) => {

        console.log("checking passwords")
        e.preventDefault();

        const form = e.currentTarget;
        const pass1 = form.elements.formPassword;
        const pass2 = form.elements.formPassword2;
        console.log(pass1.value + " " + pass2.value)
        if (pass1.value !== pass2.value) {
            pass1.setCustomValidity("Passwords dont match");
            pass1.value = "";
            pass2.value = "";
            form.reportValidity();
        } else {
            pass1.setCustomValidity("");
            onSubmit(e)
        }


    }

    const onSubmit = (e) => {
        const form = e.currentTarget;

        const user = {
            nombreLector: form.elements.formName.value,
            apellidosLector: form.elements.formSurname.value,
            usuarioLector: form.elements.formUsername.value,
            email: form.elements.formEmail.value,
            password: form.elements.formPassword.value,
        };

        onSubmitClick(user);

    }


    return (

        <fieldset style={{
            boxShadow: "2px 4px 6px 0px rgba(0,0,0,0.75)",
            padding: "1em",
            width: "100%",
        }}>
            <div className='d-flex flex-row-reverse'>
                <CloseButton onClick={onClose} />
            </div>
            <legend align="left">Signup</legend>
            <Form onSubmit={(e) => checkPassowrds(e)} style={{ textAlign: "left" }} id="signup-form">
                <Form.Group className="mb-3" controlId="formName">
                    <Form.Label>Nombre</Form.Label>
                    <Form.Control type="text" placeholder="Introduce tu nombre" name='formName' minLength={3} required />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formSurname">
                    <Form.Label>Apellidos</Form.Label>
                    <Form.Control type="text" placeholder="Introduce tu apellido" name='formSurname' minLength={3} required />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formUsername">
                    <Form.Label>Usuario</Form.Label>
                    <Form.Control type="text" placeholder="Introduce un usuario" name='formUsername' minLength={3} required
                        onChange={(e) => { checkUsernameExists(e); e.target.setCustomValidity("") }} />
                    {usernameExist && <Alert variant="danger">El usuario ya existe</Alert>}
                </Form.Group>
                <Form.Group className="mb-3" controlId="formEmail">
                    <Form.Label>Email</Form.Label>
                    <Form.Control type="email" placeholder="Introduce un email" name='formEmail' minLength={10} required
                        onChange={(e) => { checkEmailExists(e); e.target.setCustomValidity("") }} />
                    {emailExist && <Alert variant="danger">El email ya existe</Alert>}
                </Form.Group>
                <Form.Group className="mb-3" controlId="formcPassword">
                    <Form.Label>Contraseña</Form.Label>
                    <Form.Control type="password" placeholder="Password" name="formPassword" minLength={3} required onChange={(e) => e.target.setCustomValidity("")} />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formPassword2">
                    <Form.Label>Repite contraseña</Form.Label>
                    <Form.Control type="password" placeholder="Password" name='formPassword2' minLength={3} required />
                </Form.Group>
                <ButtonGroup className="mb-3" aria-label="Basic example">
                    <Button variant="primary" type="submit">
                        Submit
                    </Button>
                </ButtonGroup>

            </Form>

        </fieldset>
    );
};